html {
  color: #222;
  font-size: 1em;
  line-height: 1.4;
}

::selection {
  text-shadow: none;
  background: #b3d4fc;
}

hr {
  height: 1px;
  border: 0;
  border-top: 1px solid #ccc;
  margin: 1em 0;
  padding: 0;
  display: block;
}

audio, canvas, iframe, img, svg, video {
  vertical-align: middle;
}

fieldset {
  border: 0;
  margin: 0;
  padding: 0;
}

textarea {
  resize: vertical;
}

ul {
  padding-inline: 12px;
  list-style-type: none;
}

a:-webkit-any-link {
  color: inherit;
  cursor: pointer;
  text-decoration: none;
}

html {
  overflow-y: scroll;
}

body {
  background-color: #999;
  background-image: url("bg1.6c134706.png");
  margin: 0;
  padding: 0;
  font-family: Open Sans, Arial;
  font-size: 12px;
}

@font-face {
  font-family: icomoon;
  src: url("icomoon.4ee62ea5.eot");
  src: url("icomoon.4ee62ea5.eot#iefix") format("embedded-opentype"), url("icomoon.d956d713.svg#icomoon") format("svg"), url("icomoon.8008f22a.woff") format("woff"), url("icomoon.d035cc83.ttf") format("truetype");
}

#container {
  width: 860px;
  margin: 30px auto 20px;
}

#content {
  background-color: #fff;
  box-shadow: 0 1px 6px #00000045, inset 0 0 40px #0000000f;
}

.hidden {
  display: none;
}

.clear {
  clear: both;
  height: 0;
  visibility: hidden;
  width: 0;
  display: block;
  overflow: hidden;
}

.clearfix:after {
  clear: both;
  content: " ";
  height: 0;
  visibility: hidden;
  width: 0;
  font-size: 0;
  line-height: 0;
  display: block;
}

* html .clearfix, :first-child + html .clearfix {
  zoom: 1;
}

.extra-text {
  color: #999;
  border-top: 1px solid #ededed;
  border-bottom: 1px solid #ededed;
  margin-bottom: 20px;
  padding: 24px 0;
  font-size: 20px;
  font-weight: 300;
}

.transition {
  -o-transition: all .5s ease;
  transition: all .5s;
}

.word-wrap {
  word-wrap: break-word;
  -ms-word-break: break-all;
  word-break: break-all;
  hyphens: auto;
}

h1 {
  margin-block: 0;
  margin-inline: 0;
  font-size: 37px;
  font-weight: 900;
}

h2 {
  margin-block: 0;
  margin-inline: 0;
  font-size: 30px;
  font-weight: 900;
}

h3 {
  margin-block: 0;
  margin-inline: 0;
  font-size: 20px;
  font-weight: 600;
}

h4 {
  margin-block: 0;
  margin-inline: 0;
  font-size: 15px;
  font-weight: 400;
}

h5 {
  margin-block: 0;
  margin-inline: 0;
  font-size: 13px;
  font-weight: 400;
}

h6 {
  margin-block: 0;
  margin-inline: 0;
  font-size: 12px;
  font-weight: 500;
}

strong {
  color: #333;
  font-weight: bold;
}

h3.main-heading {
  color: #333;
  clear: both;
  border-bottom: 1px solid #ebebeb;
  margin: 0 2px 20px 0;
  padding-bottom: 14px;
  font-size: 16px;
  font-weight: 600;
  line-height: 16px;
  display: block;
  position: relative;
}

h3.main-heading:after {
  content: "";
  width: 40px;
  height: 1px;
  background: #f4bb8a;
  position: absolute;
  bottom: -1px;
  left: 0;
}

header {
  height: 65px;
  margin-bottom: 20px;
  overflow: hidden;
}

#logo {
  float: left;
  margin-left: 10px;
  display: none;
  overflow: hidden;
}

#logo h2 {
  color: #2f465a;
  text-shadow: 0 1px #ffffff80;
  font-size: 30px;
  font-weight: 900;
  line-height: 40px;
}

#logo h4 {
  color: #666;
  line-height: 20px;
}

.socialicons {
  float: right;
  margin-top: 18px;
}

.socialicons li {
  float: left;
  margin: 0 2px 0 0;
  padding: 0;
  display: inline;
}

.socialicons li a {
  color: #fff;
  background-color: #222;
  padding: 6px 8px;
  font-family: icomoon;
  font-size: 16px;
  display: block;
}

.socialicons li a:hover {
  color: #f4bb8a;
}

.social-icons li:last-child {
  margin-right: 0;
}

.socialicons .socials-text {
  height: 17px;
  color: #fff;
  background-color: #222;
  padding: 9px;
}

#profile {
  background: #96caf2 url("header-shadow.04f551f9.png") bottom repeat-x;
  overflow: hidden;
}

.about {
  float: left;
  width: 490px;
  margin-top: 30px;
  margin-left: 20px;
}

.about .photo-inner {
  float: left;
  width: 192px;
  height: 212px;
  background: url("photo-inner.bdc320fc.png") no-repeat;
  padding: 12px 0 0 20px;
}

.about .photo-inner img {
  background-color: #fff;
}

.about h1 {
  color: #778fa5;
  text-shadow: 0 1px #ffffff80;
  margin-top: 0;
  font-weight: 800;
  line-height: 38px;
}

.about h3 {
  color: #fff;
  margin: 10px 0;
  font-size: 18px;
  font-weight: 500;
  line-height: 18px;
}

.about p {
  color: #778fa5;
  margin: 5px 0;
  font-size: 14px;
  line-height: 20px;
}

.personal-info {
  float: left;
  width: 310px;
  min-height: 230px;
  background: url("personal-info-sep.f8666e2c.png") 0 0 no-repeat;
  margin-top: 30px;
  margin-left: 10px;
  padding: 20px 0 0 20px;
}

.personal-info h4 {
  color: #fff;
}

.personal-info p {
  color: #778fa5;
  margin: 10px 0;
  font-size: 14px;
  line-height: 20px;
}

.menu {
  height: 100px;
  width: 600px;
  background-color: #ededed;
  padding-left: 130px;
  padding-right: 130px;
  position: relative;
}

.tabs {
  height: 116px;
  z-index: 100;
  margin-top: -14px;
  position: absolute;
}

.tabs li {
  float: left;
  height: 116px;
}

.tabs li > a {
  width: 115px;
  color: #b9b9b9;
  text-align: center;
  background-repeat: no-repeat;
  padding-top: 23px;
  font-size: 18px;
  font-weight: 300;
  display: inline-block;
}

.tabs li.active {
  background: url("tab-hover.29ca862b.png") no-repeat;
}

.tabs li.active a {
  color: #f4bb8a;
}

.tabs li > a > i {
  margin-bottom: 5px;
  font-family: icomoon;
  font-size: 35px;
  font-style: normal;
  display: block;
}

.next, .prev {
  color: #eee;
  width: 40px;
  text-align: center;
  cursor: pointer;
  background-color: #ccc;
  font-family: icomoon;
  font-size: 34px;
  position: absolute;
  top: 30px;
}

.next {
  right: 0;
}

.prev {
  left: 0;
}

.disabled {
  display: none !important;
}

#resume, #certificates {
  padding: 40px 20px;
  overflow: hidden;
}

.skills-section, .timeline-section {
  float: left;
}

.timeline-section {
  width: 490px;
  padding-right: 60px;
}

.timeline {
  margin-bottom: 30px;
  padding: 0;
}

.timeline li {
  margin-bottom: 12px;
}

.timeline li .timelineUnit {
  color: #444;
  border-left: 1px solid #f4bb8a;
  margin-left: 0;
  padding-left: 15px;
  font-size: 13px;
  line-height: 17px;
}

.timeline li .timelineUnit .timelineDate {
  color: #f4bb8a;
  float: right;
  margin-left: 10px;
  padding: 2px 6px;
  font-size: 14px;
  font-weight: normal;
  line-height: 17px;
}

.timeline li .timelineUnit ul {
  padding-top: 12px;
  list-style-type: disc;
}

.timeline li .timelineUnit h4 {
  color: #444;
  font-size: 15px;
  font-weight: 700;
  line-height: 24px;
}

.timeline li .timelineUnit h5 {
  color: #666;
  padding-bottom: 12px;
  font-size: 13px;
  line-height: 18px;
}

.timeline li .timelineUnit h6 {
  font-style: italic;
}

.timeline li .timelineUnit p {
  color: #c3c3c3;
  margin-top: 5px;
  font-size: 12px;
}

.skills-section {
  width: 270px;
}

.skills {
  margin-bottom: 25px;
  padding: 0;
  overflow: hidden;
}

.skills li {
  float: left;
  margin-bottom: 15px;
  padding-left: 0;
}

.skills li h4 {
  width: 140px;
  float: left;
  color: #444;
  margin: 0;
  font-size: 15px;
}

.skills li .rating {
  width: 123px;
  height: 14px;
  float: left;
}

.skills li .rating > span {
  opacity: .3;
  height: 12px;
  width: 12px;
  float: left;
  background-color: #757575;
  border: 2px solid #eee;
  border-radius: 20px;
  margin-right: 1px;
  display: block;
}

#resume .button {
  width: 250px;
  color: #f7f7f7;
  text-align: center;
  background-color: #8c8c8c;
  border-radius: 0 4px 4px 0;
  margin: 20px 0 0 -20px;
  padding: 10px;
  font-size: 14px;
  font-weight: 300;
  line-height: 20px;
  display: block;
  overflow: hidden;
}

#resume .button:hover {
  background-color: #f4bb8a;
}

#portfolio {
  padding: 20px;
}

#portfolio-list {
  width: 820px;
  overflow: hidden;
}

#portfolio-list li {
  float: left;
  text-align: center;
  height: 185px;
  width: 180px;
  background-color: #fbfbfb;
  border-bottom: 1px solid #e5e5e5;
  margin-bottom: 30px;
  margin-right: 25px;
}

#portfolio-list li:hover {
  height: 185px;
  border-bottom: 1px solid #f4bb8a;
}

#portfolio-list img {
  height: 120px;
  width: 180px;
  background-color: #fff;
}

#portfolio-list li:hover .title {
  color: #f4bb8a;
}

#portfolio-list li .title {
  color: #3a3a3a;
  margin-top: 15px;
  font-size: 14px;
  font-weight: 400;
  line-height: 6px;
}

#portfolio-list li .categorie {
  color: #999;
  font-size: 11px;
  line-height: 25px;
}

.cats-filter {
  padding: 0;
  overflow: hidden;
}

.cats-filter li a {
  float: left;
  color: #fff;
  background-color: #666;
  margin-bottom: 5px;
  margin-right: 7px;
  padding: 6px;
}

.cats-filter li a:hover, .cats-filter li a.current {
  background-color: #f4bb8a;
}

.adipoli-wrapper {
  margin: auto;
  display: inline-block;
  position: relative;
}

.adipoli-wrapper > img {
  z-index: 1;
  position: absolute;
}

.adipoli-before {
  z-index: 5;
  position: absolute;
}

.adipoli-after {
  z-index: 10;
  position: absolute;
}

.adipoli-slice {
  z-index: 15;
  height: 100%;
  display: block;
  position: absolute;
}

.adipoli-box {
  z-index: 15;
  display: block;
  position: absolute;
}

#blog {
  padding: 20px;
  position: relative;
  overflow: hidden;
}

.blog-content-wrapper {
  width: 530px;
  float: left;
  min-height: 100px;
  margin-bottom: 30px;
}

.blog-entry {
  width: 100%;
  float: left;
  background-color: #f8f8f8;
  margin-bottom: 30px;
  overflow: hidden;
}

.blog-entry img {
  float: left;
  width: 530px;
  opacity: .8;
  filter: alpha(opacity= 80);
}

.blog-entry h3 {
  clear: both;
  color: #3a3a3a;
  padding: 20px 15px 15px;
  font-size: 17px;
  font-weight: 700;
  line-height: 18px;
}

.blog-entry a:hover img {
  opacity: 1;
  filter: alpha(opacity= 100);
}

.blog-entry > a:hover h3 {
  color: #f4bb8a;
}

.blog-entry .entry-date {
  background-color: #666;
  padding: 5px 10px;
  position: absolute;
  top: 5px;
  left: 0;
}

.blog-entry .entry-date .day {
  float: left;
  color: #fff;
  border-right: 1px solid #fff;
  padding-right: 10px;
  font-size: 32px;
  font-weight: bold;
  display: block;
}

.blog-entry .entry-date .month-year {
  text-align: center;
  float: left;
  color: #fff;
  margin: 0;
  padding: 0 0 0 7px;
  font-size: 12px;
  font-weight: 600;
  line-height: 15px;
  display: block;
}

.blog-entry p {
  color: #666;
  margin-bottom: 15px;
  padding: 0 15px;
  font-size: 12px;
  line-height: 20px;
}

.blog-sidebar {
  width: 260px;
  float: left;
  margin-left: 30px;
}

.blog-single {
  background-color: #fff;
  position: relative;
}

.blog-single h3, .blog-single p {
  padding-left: 0 !important;
}

.comments-list {
  margin-bottom: 30px;
}

.comment {
  background-color: #fbfbfb;
  border-left: 1px solid #f4bb8a;
  margin-bottom: 10px;
  padding: 12px 9px;
  overflow: hidden;
}

.comment-child {
  margin-left: 20px;
}

.comment-child .comment-meta, .comment-child .comment-body {
  width: 430px !important;
}

.comment img {
  float: left;
}

.comment .comment-meta, .comment .comment-body {
  float: left;
  width: 450px;
  margin-left: 10px;
}

.comment .comment-meta h4 {
  color: #333;
  font-weight: 400;
  display: inline-block;
}

.comment .comment-meta p {
  color: #ccc;
  margin-left: 15px;
  font-size: 11px;
  font-style: italic;
  display: inline-block;
}

.comment .comment-meta .reply {
  float: right;
  color: #ddd;
}

.comment .comment-meta .reply:hover {
  color: #f4bb8a;
}

.comment .comment-body p {
  color: #909090;
  margin-top: 8px;
  font-size: 11px;
  line-height: 16px;
}

#page_nav {
  width: 150px;
  color: #f7f7f7;
  text-align: center;
  background-color: #8c8c8c;
  border-radius: 0 4px 4px 0;
  margin: 10px 0 0 -20px;
  padding: 6px;
  font-size: 14px;
  font-weight: 300;
  line-height: 20px;
  display: block;
  overflow: hidden;
}

#page_nav:hover {
  background-color: #f4bb8a;
}

.loading-msg img {
  display: none;
}

.popular-posts-widget {
  width: 100%;
  margin-bottom: 30px;
  overflow: hidden;
}

.popular-posts-widget li {
  background-color: #fbfbfb;
  margin-bottom: 4px;
  padding: 10px;
}

.popular-posts-widget li:last-child {
  border-bottom: none;
}

.popular-posts-widget li h2 {
  margin-bottom: 4px;
  font-size: 13px;
  line-height: 20px;
}

.popular-posts-widget li h2 a:hover {
  color: #f4bb8a;
}

.popular-posts-widget li span {
  color: #999;
  font-size: 11px;
}

.flicker-stream-widget {
  margin-bottom: 30px;
  overflow: hidden;
}

.flicker-stream-widget li {
  float: left;
  margin-bottom: 5px;
  margin-right: 5px;
}

.flicker-stream-widget li img {
  height: 60px;
  width: 60px;
  opacity: .6;
}

.flicker-stream-widget li img:hover {
  opacity: 1;
}

.loading {
  color: #333;
  background-color: #ededed;
  border-radius: 3px;
  padding: 10px 20px;
  font-size: 14px;
  display: none;
}

.blog-single-close {
  float: right;
  color: #fff;
  color: #ddd;
  font-size: 14px;
  font-weight: 300;
}

.blog-single-close i {
  vertical-align: top;
  font-family: icomoon;
  font-size: 18px;
}

.blog-single-close:hover {
  color: #f4bb8a;
}

.text-widget {
  padding-bottom: 30px;
  line-height: 20px;
}

#contact {
  padding: 30px 0;
  overflow: hidden;
}

.contact-info, .contact-form {
  float: left;
  padding: 0 20px;
}

.contact-info {
  width: 250px;
}

.contact-form {
  width: 530px;
}

.contact-info ul {
  float: left;
  border-left: 1px solid #f4bb8a;
  padding-left: 18px;
}

.contact-info ul li {
  color: #666;
  font-size: 13px;
  line-height: 20px;
  overflow: hidden;
}

#map {
  height: 300px;
  width: 100%;
  border-top: 1px solid #ccc;
  border-bottom: 1px solid #ccc;
  margin-bottom: 25px;
}

form .input, form textarea {
  color: #666;
  height: 30px;
  resize: none;
  width: 92%;
  background-color: #fdfdfd;
  border: 1px solid #d9d9d9;
  border-radius: 2px;
  outline: none;
  padding: 6px 10px;
  font-size: 12px;
  box-shadow: inset 0 5px 16px -13px #333;
}

form textarea {
  height: 112px;
  margin-bottom: 0;
  padding-top: 8px;
  line-height: 18px;
}

form p {
  margin-bottom: 15px;
}

form p label {
  color: #999;
  margin-bottom: 8px;
  font-size: 13px;
  display: block;
}

.button {
  color: #fff;
  cursor: pointer;
  background-color: #666;
  border: none;
  border-radius: 3px;
  padding: 8px;
  font-size: 13px;
}

.button:hover {
  background-color: #f4bb8a;
}

.success, .error {
  border-radius: 3px;
  margin-bottom: 10px;
  padding: 10px;
}

.success {
  color: #8fa442;
  background-color: #e3ebc6;
  border: 1px solid #c2d288;
}

.error {
  color: #b3696c;
  background-color: #f9e5e6;
  border: 1px solid #f7c7c9;
}

footer {
  height: 20px;
  margin-top: 20px;
}

footer .copyright {
  float: left;
  color: #333;
  padding-left: 6px;
}

div.footer {
  height: 20px;
  margin-top: 20px;
}

div.footer .copyright {
  float: left;
  color: #333;
  padding-left: 6px;
}

.isotope-item {
  z-index: 2;
}

.isotope-hidden.isotope-item {
  pointer-events: none;
  z-index: 1;
}

.isotope, .isotope .isotope-item {
  transition-duration: .8s;
}

.isotope {
  transition-property: height, width;
}

.isotope .isotope-item {
  -webkit-transition-property: -webkit-transform, opacity;
  -moz-transition-property: -moz-transform, opacity;
  transition-property: transform, opacity;
}

@media only screen and (min-width: 768px) and (max-width: 959px) {
  #container {
    width: 750px;
  }

  .menu {
    height: 100px;
    width: 590px;
    background-color: #ededed;
    padding-left: 80px;
    padding-right: 80px;
    position: relative;
  }

  .about {
    width: 410px;
  }

  .about h1 {
    font-size: 30px;
    line-height: 38px;
  }

  .about h3 {
    font-size: 14px;
    line-height: 14px;
  }

  .personal-info {
    width: 280px;
    margin-right: 10px;
  }

  .personal-info li span {
    width: 190px;
  }

  .timeline-section {
    width: 380px;
    padding-right: 30px;
  }

  .skills-section {
    width: 300px;
  }

  #portfolio-list {
    width: 750px;
  }

  #portfolio-list li {
    margin-right: 60px;
  }

  .blog-content-wrapper, .blog-entry img {
    width: 420px;
  }

  .comment .comment-meta, .comment .comment-body {
    float: left;
    width: 330px;
    margin-left: 10px;
  }

  .comment-child .comment-meta, .comment-child .comment-body {
    width: 310px !important;
  }

  .contact-info {
    width: 250px;
  }

  .contact-form {
    width: 420px;
  }
}

@media only screen and (min-width: 480px) and (max-width: 767px) {
  #container {
    width: 470px;
  }

  #logo h2 {
    color: #444;
    font-size: 25px;
    font-weight: 900;
    line-height: 30px;
  }

  #logo h4 {
    color: #666;
    line-height: 15px;
  }

  .socialicons {
    margin-top: 9px;
  }

  .socials-text {
    display: none !important;
  }

  .about {
    width: 420px;
    display: block;
  }

  .about h1 {
    font-size: 30px;
    line-height: 38px;
  }

  .about h3 {
    font-size: 14px;
    line-height: 14px;
  }

  .personal-info {
    width: 420px;
    background: none;
    border-top: 1px solid #f4bb8a;
    margin-top: 0;
    display: block;
  }

  .personal-info li span {
    width: 280px;
  }

  .tabs {
    padding-left: 5px;
  }

  .menu {
    height: 100px;
    width: 370px;
    background-color: #ededed;
    padding-left: 50px;
    padding-right: 50px;
    position: relative;
  }

  .skills-section {
    width: 420px;
  }

  .skills li h4 {
    width: 260px;
  }

  .timeline-section {
    width: 420px;
    padding-right: 60px;
