	}
	/*-------------------------------------------------------*/
		/* Top Section */
	/*-------------------------------------------------------*/
	header {
		height:65px;
		margin-bottom:20px;
		overflow:hidden;
	}
	/* Logo */
	#logo {
		float:left;
		overflow:hidden;
		margin-left:10px;
		display:none;
	}
	#logo h2 {
    font-size: 30px;
    font-weight: 900;
    color: #2F465A;
    text-shadow: rgb(255 255 255 / 50%) 0px 1px 0px;
    line-height:40px;
	}

	#logo h4 {
		color:#666;
		line-height:20px;
	}
	/* Social icons */
	.socialicons {
		float:right;
		margin-top:18px;
	}
	.socialicons li {
		float:left;
		display:inline;
		margin:0;
		padding:0;
		margin-right:2px;
	}
	.socialicons li a  {
		font-family: 'icomoon';
		font-size: 16px;
		display: block;
		padding: 6px 8px;
		background-color:#222;
		color: #FFF;
	}
	.socialicons li a:hover  {
		color:#F4BB8A;
	}
	.social-icons li:last-child {
		margin-right:0
	}
	.socialicons .socials-text {
		padding:9px;
		height:17px;
		background-color:#222;
		color:#FFF;
	}
	/*-------------------------------------------------------*/
		/* Profile Section */
	/*-------------------------------------------------------*/
    #profile {
		background:/*mainColor*/#96CAF2;
		background-image:url(../images/header-shadow.png);
		background-position:center bottom;
		background-repeat:repeat-x;
		overflow:hidden;
	}
	/* About */
	.about {
		float:left;
		margin-top:30px;
		width:490px;
		margin-left:20px;
	}
	.about .photo-inner {
		float:left;
		background:url(../images/photo-inner.png) no-repeat;
		width:192px;
		height:212px;
		padding:12px 0 0 20px;
	}
	.about .photo-inner img {
		background-color:#FFF;
	}
	.about h1 {
		font-weight: 800;
    color: #778FA5;
    text-shadow: rgb(255 255 255 / 50%) 0px 1px 0px;
    line-height: 38px;
    margin-top: 0px;
	}
	.about h3 {
		margin: 10px 0px;
    font-size: 18px;
    color: #FFF;
    line-height: 18px;
		font-weight: 500;
	}
	.about p {
		font-size: 14px;
    margin: 5px 0px;
    color: #778FA5;
    line-height: 20px;
	}
	/* Personal info */
	.personal-info {
		float:left;
		margin-top:30px;
		width:310px;
		min-height:230px;
		margin-left:10px;
		background:url(../images/personal-info-sep.png) no-repeat left top;
		padding:20px 0 0 20px;
	}
	.personal-info h4 {
		color: white;
	}
	.personal-info p {
		font-size: 14px;
    margin: 10px 0px;
    color: #778FA5;
    line-height: 20px;
	}
	/*-------------------------------------------------------*/
		/* Menu */
	/*-------------------------------------------------------*/
	.menu {
		height:100px;
		background-color:#ededed;
		position:relative;
		width: 600px;
		padding-left: 130px;
		padding-right: 130px;
	}
	.tabs {
		height:116px;
		position: absolute;
		margin-top:-14px;
		z-index:100;
	}
	.tabs li {
		float:left;
		height:116px;
	}
	.tabs li > a {
		padding-top:23px;
		width:115px;
		display:inline-block;
		background-repeat:no-repeat;
		font-size:18px;
		color:#b9b9b9;
		text-align:center;
		font-weight: 300;
	}
	.tabs li.active {
		background:url('../images/tab-hover.png') no-repeat;
	}
	.tabs li.active a {
		color:#F4BB8A;
	}

	.tabs li > a > i {
		font-size: 35px;
		font-family: 'icomoon';
    font-style: normal;
		display: block;
		margin-bottom: 5px;
	}

	.next,.prev {
		font-family: 'icomoon';
		font-size: 34px;
		color: #eee;
		position: absolute;
		top:30px;
		width: 40px;
		background-color: #ccc;
		text-align: center;
		cursor: pointer;
	}
	.next {
		right:0px;
	}
	.prev {
		left:0px;
	}
	.disabled { display: none !important;  }


	/*-------------------------------------------------------*/
		/* Resume Section */
	/*-------------------------------------------------------*/
  #resume,
	#certificates {
		padding:40px 20px;
		overflow:hidden;
	}
	.skills-section, .timeline-section {
		float:left;
	}
	/* Timeline Section */
	.timeline-section {
		width:490px;
		padding-right:60px;
	}
	.timeline {
		margin-bottom: 30px;
    padding: 0;
	}
	.timeline li {
		margin-bottom:12px;
	}
	.timeline li .timelineUnit {
		line-height:17px;
		margin-left:0px;
		color: #444;
		font-size:13px;
		border-left:1px solid #F4BB8A;
		padding-left:15px;
	}
	.timeline li .timelineUnit .timelineDate {
		line-height:17px;
		color:#F4BB8A;
		font-size:14px;
		margin-left:10px;
		font-weight: normal;
		padding:2px 6px;
		float:right;
	}
	.timeline li .timelineUnit ul {
		padding-top: 12px;
		list-style-type: disc;
	}
	.timeline li .timelineUnit h4 {
		line-height:24px;
		font-size:15px;
		font-weight: 700;
		color: #444;
	}
	.timeline li .timelineUnit h5 {
		line-height:18px;
		font-size:13px;
		color: #666;
		padding-bottom: 12px;
	}
	.timeline li .timelineUnit h6 {
		font-style: italic;
	}
	.timeline li .timelineUnit p {
		color: #c3c3c3;
		font-size:12px;
		margin-top:5px;
	}

	/* Skills Section */
	.skills-section {
		width:270px;
	}
	.skills {
		margin-bottom: 25px;
		overflow:hidden;
    padding: 0;
	}
	.skills li {
		margin-bottom:15px;
		float:left;
		padding-left:0px;
	}
	.skills li h4 {
		width:140px;
		float:left;
		font-size:15px;
		color: #444;
    margin: 0;
	}

	.skills li .rating {
		width:123px;
		height:14px;
		float:left;
	}
	.skills li .rating > span {
		opacity: 0.3;
		height:12px;
		width:12px;
		background-color:#757575;
		display: block;
		float: left;
		margin-right: 1px;
		border: 2px solid #eeeeee;
		  -webkit-border-radius: 20px;
		     -moz-border-radius: 20px;
		          border-radius: 20px;
	}

	/* Download Button */
    #resume .button {
    	width: 250px;
    	padding: 10px;
    	background-color: #8c8c8c;
    	overflow: hidden;
    	font-size: 14px;
    	font-weight: 300;
    	line-height: 20px;
    	margin: 20px 0px 0px -20px;
    	color: #f7f7f7;
    	display: block;
    	text-align: center;
		  -webkit-border-radius: 0px 4px 4px 0px;
		     -moz-border-radius: 0px 4px 4px 0px;
		          border-radius: 0px 4px 4px 0px;
	}
    #resume .button:hover {
    	background-color:#F4BB8A;
	}

	/*-------------------------------------------------------*/
		/* Portfolio Section */
	/*-------------------------------------------------------*/
    #portfolio {
		padding:20px 20px 20px 20px;
	}
	/* Images list */
	#portfolio-list {
		width:820px;
		overflow:hidden;
	}
	#portfolio-list li {
		float:left;
		margin-right:25px;
		text-align:center;
		background-color:#fbfbfb;
		border-bottom:1px solid #e5e5e5;
		height:185px;
		margin-bottom:30px;
		width:180px;
	}
	#portfolio-list li:hover {
		border-bottom:1px solid #F4BB8A;
		height:185px;
	}
	#portfolio-list img {
		background-color:#FFF;
		height:120px;
		width:180px;
	}
	#portfolio-list li:hover .title {
		color:#F4BB8A;
	}
	#portfolio-list li .title {
		font-size:14px;
		margin-top:15px;
		font-weight: 400;
		color: #3A3A3A;
    line-height: 6px;
	}
	#portfolio-list li .categorie {
		font-size:11px;
		line-height:25px;
		color:#999;
	}

	/* Filter menu */

	.cats-filter {
		overflow:hidden;
		padding:0;
	}
	.cats-filter li a {
		float:left;
		margin-right:7px;
		color:#fff;
		background-color: #666;
		padding:6px;
		margin-bottom:5px;
	}
	.cats-filter li a:hover {
		background-color:#F4BB8A;
	}
	.cats-filter li a.current {
		background-color:#F4BB8A;
	}
	/* adipoli */
		.adipoli-wrapper {
		margin:auto;
		position:relative;
		display: inline-block;
	}
	.adipoli-wrapper>img {
		position: absolute;
		z-index: 1;
	}
	.adipoli-before {
		position: absolute;
		z-index: 5;
	}
	.adipoli-after {
		position: absolute;
		z-index: 10;
	}
	.adipoli-slice {
		display:block;
		position:absolute;
		z-index:15;
		height:100%;
	}
	.adipoli-box {
		display:block;
		position:absolute;
		z-index:15;
	}
	/*-------------------------------------------------------*/
		/* Blog Section */
	/*-------------------------------------------------------*/
    #blog {
		padding:20px;
		position: relative;
		overflow: hidden;
	}
	.blog-content-wrapper {
		width: 530px;
		float: left;
		min-height: 100px;
		margin-bottom: 30px;
	}

    .blog-entry {
    	overflow: hidden;
    	width: 100%;
    	margin-bottom: 30px;
        background-color: #f8f8f8;
        float: left;

	}
    .blog-entry img {
    	float: left;
    	width: 530px;
    	opacity: 0.8;
    	filter:alpha(opacity=80);
	}
    .blog-entry h3 {
    	clear: both;
		padding:20px 15px 15px 15px;
		font-size: 17px;
		line-height: 18px;
		font-weight: 700;
		color: #3A3A3A;
	}
    .blog-entry a:hover img {
    	opacity: 1;
    	filter:alpha(opacity=100);
	}
    .blog-entry > a:hover h3 {
    	color:#F4BB8A;
	}

	/* Entry Date */
    .blog-entry .entry-date {
    	padding: 5px 10px;
    	position: absolute;
    	top: 5px;
    	left: 0px;
    	background-color: #666;
	}
    .blog-entry .entry-date .day {
    	font-size: 32px;
    	font-weight:bold;
    	display: block;
    	float: left;
    	border-right:1px solid #FFF;
    	padding-right: 10px;
    	color: #fff;
	}
    .blog-entry .entry-date .month-year {
    	padding: 0 0 0 7px ;
    	font-size: 12px;
    	font-weight: 600;
    	display: block;
    	line-height: 15px;
    	text-align: center;
    	float: left;
    	color: #fff;
    	margin: 0;
	}
    .blog-entry p {
		padding:0px 15px;
		font-size: 12px;
		color: #666;
		line-height: 20px;
		margin-bottom: 15px;
	}

	/* Blog Sidebar */
	.blog-sidebar {
		width: 260px;
		float: left;
		margin-left:30px;
	}
	/* Blog Single */
    .blog-single {
    	position: relative;
    	background-color:#fff;
	}
    .blog-single h3,.blog-single p {
		padding-left:0px !important;
	}
	/* Comments box */
	.comments-list {
		margin-bottom: 30px;
	}
    .comment {
    	background-color: #fbfbfb;
    	padding: 12px 9px;
    	overflow: hidden;
    	border-left:1px solid #F4BB8A;
    	margin-bottom: 10px;
	}
    .comment-child {
    	margin-left: 20px;
	}
    .comment-child .comment-meta,.comment-child .comment-body {
    	width: 430px !important;
	}
    .comment img {
    	float: left;
	}
    .comment .comment-meta,.comment .comment-body {
    	float: left;
    	width: 450px;
    	margin-left: 10px;
	}
    .comment .comment-meta h4 {
    	font-weight: 400;
    	display: inline-block;
    	color: #333;
	}
    .comment .comment-meta p {
    	display: inline-block;
    	font-size: 11px;
    	color: #ccc;
    	font-style: italic;
    	margin-left: 15px;
	}
    .comment .comment-meta .reply {
    	float: right;
    	color: #ddd;
	}
    .comment .comment-meta .reply:hover {
    	color:#F4BB8A;
	}
    .comment .comment-body p {
    	margin-top: 8px;
    	font-size: 11px;
    	color: #909090;
      	line-height: 16px;
	}

	/* Load More Button */
    #page_nav {
    	width: 150px;
    	padding: 6px;
    	background-color: #8c8c8c;
    	overflow: hidden;
    	font-size: 14px;
    	font-weight: 300;
    	line-height: 20px;
    	margin: 10px 0px 0px -20px;
    	color: #f7f7f7;
    	display: block;
    	text-align: center;
		  -webkit-border-radius: 0px 4px 4px 0px;
		     -moz-border-radius: 0px 4px 4px 0px;
		          border-radius: 0px 4px 4px 0px;
	}
    #page_nav:hover {
    	background-color:#F4BB8A;
	}

	/* Loading Text */
    .loading-msg img {
    	display: none;
	}
